/* // <uniquifier>: Use a unique and descriptive class name
// <weight>: Use a value from 100 to 400 */

.spanclass {
  font-family: "Playwrite HR", cursive;
  font-optical-sizing: auto;
  /* font-weight: <weight>; */
  font-style: normal;
}

.textbox {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  transition: border-color 0.3s, box-shadow 0.3s;
  text-align: center;
}

.textbox:focus {
  border-color: #3802ce;
  box-shadow: 0 0 8px rgba(247, 46, 230, 0.898);
  text-align: center;
  outline: none;
}

.buttons {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 1000;
  color: #020101;
  border: 1px solid rgb(1, 13, 255);
  background-size: cover;
  background-position: center;
  cursor: pointer;
  transition: opacity 0.3s;
}

.address{
  color: blue;
  text-align: center;
  
}

.buttons:hover {
  opacity: 0.8;
  background-color: rgb(255, 4, 213);
}


.content{
  /* border: 1px solid grey; */
  padding: 2%;
  border-radius: 10px;
  box-shadow: 5px 5px 5px rgb(246, 0, 246), -5px -5px 5px blue;


}