.container {
    width: 60%; /* Ensure the container takes up a maximum of 70% of the screen */
    margin-left: 20%; /* Center the container horizontally */
    height: auto; /* Adjust height as needed */
    padding-top: 25%;   
    margin-top: 4%;
    margin-left: 30%;
    /* border: 1px solid black; */
    position: relative;
    border-radius: 2%;
    overflow: hidden; /* Ensure the pseudo-element doesn't overflow */
  }
  
  .container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../../images/contact_box.png');
    background-size: 80%; /* Increase the background image size */
    background-position: center center; /* Center the image both horizontally and vertically */
    background-repeat: no-repeat;
    z-index: -1; /* Ensure the pseudo-element is behind the content */
    animation: rotateBackground 10s linear infinite; /* Add animation */
  }
  
  @keyframes rotateBackground {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .subcontainer {
    /* border: 1px solid rgb(237, 18, 18); */
    position: relative;
    padding: 20px; /* Add padding to ensure content is within the background image */
  }
  
  .ulblock {
    list-style: none;
    padding: 0;
    margin: 0;
    text-align: center; /* Center the list items */
    position: relative;
    bottom: 180px;
    color: white;
    font-size: 20px;
  }
  